import React from "react";
import styled from "../../typed-components";
import Button from "../Button";
import { useQuery } from "react-apollo-hooks";
import { USER_PROFILE } from "src/SharedQueries";

interface IProps {
  pickUpAddress: string;
  dropOffAddress: string;
  price: number;
  distance: string;
  acceptRideFn: any;
}

const Container = styled.div`
  background-color: white;
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 80%;
  height: 60%;
  z-index: 9;
  padding: 20px;
`;

const Title = styled.h4`
  font-weight: 800;
  margin-top: 30px;
  margin-bottom: 10px;
  &:first-child {
    margin-top: 0;
  }
`;

const Data = styled.span`
  color: ${props => props.theme.blueColor};
`;

const Img = styled.img`
  border-radius: 50%;
  margin-right: 5px;
  height: 60px;
  width: 60px;
`;

const Passenger = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 20px;
`;

const RidePopUp: React.SFC<IProps> = ({
  pickUpAddress,
  dropOffAddress,
  price,
  distance,
  acceptRideFn
}) => {

  const {data, loading} = useQuery(USER_PROFILE);

  console.log(`data : ${JSON.stringify(data)}`);

  return (
      <Container>
        <Title></Title>
        <Data>{pickUpAddress}</Data>
        <Title></Title>
        <Data>{dropOffAddress}</Data>
        <Title></Title>
        <Data>{price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </Data>
        <Title></Title>
        <Data>{distance}</Data>
        <Title>:</Title>
        <Passenger>
          <Img src={loading ? "" : data.GetMyProfile.user.profilePhoto} />
          <Data>{loading ? "..." : data.GetMyProfile.user.fullName}</Data>
        </Passenger>
        <Button
          onClick={() => acceptRideFn()}
          value={" "}
        />
      </Container>
  );
}

export default RidePopUp;



